<template>
    <vue-typeahead-bootstrap
      class="mb-4"
      v-model="query"
      :ieCloseFix="false"
      :data="software"
      :serializer="(software) => software.name + ' (Category:'+software.category+' in '+software.platform+')'"
      @hit="onSelect($event)"
      :disabledValues="selecteduser ? [selecteduser.name] : []"
      placeholder="Which alternative software you try it before?"
      @input="lookupUser"
    />
    <!-- <div class="pl-1 pb-2 pt-3">
      Selected name: <span v-if="selecteduser">{{ selecteduser.name }}</span>
    </div> -->
</template>

<script>
import { debounce } from "lodash";

export default {
  name: "Example",
  props: {
    selectedSoftware: Object,
  },
  data() {
    return {
      query: "",
      selecteduser: null,
      software: []
    };
  },

  methods: {
    onSelect: function(event){
      this.selecteduser = event;
      console.log(event);
      this.$emit('onSelectSoftware', event)
    },
    lookupUser: debounce(function () {
      // in practice this action should be debounced
      fetch(`http://localhost:28888/gangchanager/softwares?name=${this.query}&platform=${this.selectedSoftware.platform}`)
      //fetch(`https://api.github.com/search/users?q=${this.query}`)
        .then((response) => {
          return response.json();
        })
        .then((res) => {
          this.software = res.data;
        });
    }, 500),
  },
};
</script>