<template>
  <div class="row p-2">
    <div class="col-md-2">
      <v-icon name="compact-disc" scale="3" class="image-alt m-1" />
    </div>
    <div class="col-md-9">
      <h4>{{ software.name }}</h4>
      <div>
          <span class="badge badge-pill badge-primary">{{software.category}}</span>
          <span class="ml-2 badge badge-pill badge-secondary">{{software.platform}}</span>
      </div>
      {{ software.description }}
      <div v-if="this.alternative" class="mt-3">
        <AlternativeTypeahead @onSelectSoftware="onSelectSoftware"  v-bind:selectedSoftware="this.software"></AlternativeTypeahead>
        <!-- <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">Alternative</span>
        </div>
        <input type="text" class="form-control" placeholder="Software Name"
          aria-label="Alternative Software Name" aria-describedby="basic-addon1"> -->
      </div>
      <div v-if="this.alternative" class="input-group">
        <div class="input-group-prepend">
          <span class="input-group-text">Reason</span>
        </div>
        <textarea v-model="alternativeSoftware.reason" class="form-control" aria-label="With textarea" placeholder="Why do you think the alternative cannot suit you?"></textarea>
      </div>
    </div>
    <div class="col-md-1">
      <button v-on:click="onClickAlternative()" type="button" class="btn btn-light float-right cross">
        <v-icon name="check" scale="2" />
      </button>
    </div>
  </div>
</template>

<script>
  import AlternativeTypeahead from "./AlternativeTypeahead.vue";
  export default {
    name: "InterestSoftware",
    components: {
      AlternativeTypeahead
    },
    methods: {
      getData: function(){
        this.alternativeSoftware.software = this.software;
        return this.alternativeSoftware;
      },
      onSelectSoftware: function(selected){
        this.alternativeSoftware.alternative = selected;
      },
      onClickAlternative: function () {
        this.alternative = !this.alternative;
      },
    },
    props: {
      software: {},
    },
    data(){
      return {
        alternative: false,
        alternativeSoftware:{
          software: null,
          reason: ''
        }
      };
    }
  };
</script>
<style scoped>
  .cross {
    color: #AAA;
  }

  .image-alt {
    color: #81B0BD;
  }
</style>