<template>
  <div>
    <div v-show="!this.afterConfirmed" class="container">
      <div class="row">
        <div class="col-md-12">
          <SoftwaresTypeahead
            @onSelectSoftware="onSelectSoftware"
          ></SoftwaresTypeahead>
        </div>
        <div class="col-md-12">
          <div
            class="row"
            v-for="software in selectedSoftware"
            v-bind:key="software.id"
          >
            <div class="col-md-12">
              <SelectedSoftware
                v-bind:software="software"
                @onRemove="onRemoveSoftware"
              ></SelectedSoftware>
            </div>
          </div>
        </div>
      </div>
      <div class="row my-3">
        <div class="col-md-12 text-center">
          <button v-on:click="onClickMoreSoftware" class="btn btn-secondary">
            Or Choose more from most popular app.
          </button>
        </div>
      </div>
      <div class="row my-3 p-3">
        <div
          class="col-md-3 p-1 click"
          v-for="software in popularSoftwares"
          v-bind:key="software.id"
          v-on:click="onCheckPopularSoftware(software, $event)"
        >
          <v-icon name="check" scale="1" />
          <span class="ml-2">{{ software.name }}</span>
        </div>
      </div>
      <div v-if="this.selectedSoftware.length > 0" class="row mb-5">
        <div class="col-md-8 offset-md-2 text-center">
          <button v-on:click="onClickShowMeDiscount" class="btn btn-info">
            Show me the discount
          </button>
        </div>
      </div>
    </div>
    <div v-show="this.afterConfirmed" class="container">
      <div class="row mb-5">
        <div class="col-md-8 offset-md-2">
          <div class="text-center mb-5">
            <h4>
              Please check if you tried to find alternative in listing
              softwares.
            </h4>
          </div>
          <div
            class="row my-1"
            v-for="software in selectedSoftware"
            v-bind:key="software.id"
          >
            <div class="col-md-12">
              <InterestSoftware
                v-bind:software="software"
                ref="interestSoftwareRef"
              ></InterestSoftware>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2">
          <div class="text-center mb-3">
            <h4 class="text-center">
              Do you want discounts for some software that is not listed?<br />Please
              let us know.
            </h4>
          </div>
          <form>
            <div class="form-group">
              <label for="exampleFormControlInput1">Software Name</label>
              <input
                type="email"
                class="form-control"
                placeholder="Software Name"
              />
            </div>
            <div class="form-group">
              <label for="exampleFormControlSelect1">Platform</label>
              <select class="form-control">
                <option>Any</option>
                <option>Chrome Plugin</option>
                <option>Shopify</option>
              </select>
            </div>
          </form>
        </div>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2">
          <h4 class="float-right">You got 10% discount.</h4>
        </div>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2">
          <form>
            <div class="form-group input-group-lg">
              <label class="font-weight-bold" for="email"
                >Notify me for discount</label
              >
              <input
                id="email"
                v-model="survey.email"
                type="email"
                class="form-control"
                placeholder="Email"
              />
            </div>
          </form>
        </div>
        <div class="col-md-8 offset-md-2 mb-5">
          <form>
            <div class="form-group">
              <button
                @click.prevent="join()"
                class="btn btn-lg btn-primary btn-block"
              >
                Join
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import SoftwaresTypeahead from "./SoftwaresTypeahead.vue";
import SelectedSoftware from "./SelectedSoftware.vue";
import InterestSoftware from "./InterestSoftware.vue";
// Required dependency of bootstrap css/scss files

export default {
  name: "WishCollectionBoard",
  components: {
    SoftwaresTypeahead,
    SelectedSoftware,
    InterestSoftware,
  },
  methods: {
    join() {
      //this.survey.selectedSoftware = this.selectedSoftware;
      if (this.survey.email != null) {
        let tempList = [];
        this.$refs.interestSoftwareRef.forEach(function(i) {
          tempList.push(i.getData());
          console.log(i.getData());
        });
        Array.prototype.push.apply(this.survey.choices, tempList);
        this.axios
          .post("http://localhost:28888/gangchanager/survey", this.survey)
          .then((res) => res.data)
          .then((res) => {
            console.log(res);
            // this.popularSoftwares.length = 0;
            // res.data.forEach((element) => {
            //   this.popularSoftwares.push(element);
            // });
          });
      }
    },
    onClickShowMeDiscount() {
      this.afterConfirmed = true;
    },
    onCheckPopularSoftware(software, event) {
      event.currentTarget.classList.toggle("click-active");
      if (!this.selectedSoftware.some((sw) => sw.id === software.id)) {
        this.selectedSoftware.push(software);
      } else {
        var i = this.selectedSoftware.indexOf(software);
        this.selectedSoftware.splice(i, 1);
      }
    },
    onClickMoreSoftware() {
      this.axios
        .get("http://localhost:28888/gangchanager/popular/softwares")
        .then((res) => res.data)
        .then((res) => {
          this.popularSoftwares.length = 0;
          res.data.forEach((element) => {
            this.popularSoftwares.push(element);
          });
        });
    },
    onRemoveSoftware(id) {
      var index = this.selectedSoftware.findIndex(function(o) {
        return o.id === id;
      });
      if (index !== -1) this.selectedSoftware.splice(index, 1);
    },
    onSelectSoftware(event) {
      if (!this.selectedSoftware.some((sw) => sw.id === event.id)) {
        this.selectedSoftware.push(event);
      }
    },
  },
  data() {
    return {
      selectedSoftware: [],
      popularSoftwares: [],
      afterConfirmed: false,
      survey: {
        email: null,
        choices: [],
      },
    };
  },
  props: {},
};
</script>
<style scoped>
.click {
  cursor: pointer;
}

.click > svg {
  color: #ccc;
}

.click-active {
  color: rgb(36, 194, 160);
  cursor: pointer;
}

.click-active > svg {
  color: rgb(36, 194, 160);
}

.click-active > span {
  font-weight: bold;
}
</style>
