<template>
  <div class="row m-1 p-2 block">
    <div class="col-md-2">
      <v-icon name="heart" scale="2"  class="image-alt m-1"/>
    </div>
    <div class="col-md-9">
      <h4>{{ software.name }}</h4>
      <div>
        {{ software.description }}
      </div>
      <div>
          <span class="badge badge-pill badge-primary">{{software.category}}</span>
          <span class="ml-2 badge badge-pill badge-secondary">{{software.platform}}</span>
      </div>
    </div>
    <div class="col-md-1">
      <button v-on:click="onRemove()" type="button" class="btn btn-light float-right cross">
        <v-icon name="times" scale="1" />
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "SelectedSoftware",
  methods: {
    onRemove: function () {
      this.$emit("onRemove", this.software.id);
    },
  },
  props: {
    software: Object,
  },
};
</script>
<style scoped>
    .cross{
        color: #AAA;
    }
    .image-alt{
        color: #ff5757;
    }
    .block{
      background-color: #f5f5f5;
      border-radius: .5rem;
    }
</style>
